import MgtvApi from 'MgtvApi';
/* import Vue from 'vue'; */
import Env from './env';
import { getCookie } from './index';

/* 获取M站登录 token */
export function getMphoneToken() {
  const hdcn = getCookie('HDCN');
  if (hdcn && typeof hdcn === 'string') {
    return hdcn.substring(0, 20);
  }
}

/* 判断登录 */
export function judgeLogin(callback) {
  if (Env.isLocal) {
    // 该代码方便本地调试
    callback();
  } else if (Env.isMgtvApp) { // app内登录
    MgtvApi.getUserInfo((data) => {
      let nowData = null;
      if (typeof data === 'string' && data) {
        nowData = JSON.parse(data);
      }
      if (!data || (nowData && !nowData.ticket)) {
        // 如果没登录
        MgtvApi.login(() => {
          callback();
        });
      } else {
        callback();
      }
    });
  } else { // M站登录
    const hdcn = getMphoneToken();
    const nowUrl = window.location.href;
    if (!hdcn) {
      window.location.href = `https://m.mgtv.com/login/?from=${encodeURIComponent(nowUrl)}`;
    } else {
      callback();
    }
  }
}


/* 登录 */
export function gotoLogin() {
  if (Env.isLocal) {
    return;
  }
  if (Env.isMgtvApp) {
    MgtvApi.login();
  } else {
    const nowUrl = window.location.href;
    window.location.href = `https://m.mgtv.com/login/?from=${encodeURIComponent(nowUrl)}`;
  }
  /* Vue.prototype.$message.confirm('登录已失效，确认前往登录?').then(() => {
    if (Env.isMgtvApp){
      MgtvApi.login()
    }else{
      const nowUrl = window.location.href
      window.location.href= `https://m.mgtv.com/login/?from=${encodeURIComponent(nowUrl)}`
    }
  }); */
}


/* 获取用户信息 */
export function getMgtvUserTicket() {
  return new Promise((resolve) => {
    if (Env.isLocal) {
      /* 本地调试 */
      resolve({
        ticket: '1R4X4466XVYUUPOSCEED', //  测试时可以填写测试token
      });
    } else if (Env.isMgtvApp) {
      MgtvApi.getUserInfo((data) => {
        if (data) {
          resolve(JSON.parse(data));
        } else {
          resolve({ ticket: null });
        }
      });
    } else {
      resolve({ ticket: getMphoneToken() });
    }
  });
}
