
import 'amfe-flexible';
import INoBounce from 'inobounce';
import 'babel-polyfill';
import Vue from 'vue';
import Es6Promise from 'es6-promise';
import { Button, Toast, Swipe, SwipeItem, Popup, MessageBox, DatetimePicker, IndexList, Spinner, Cell, IndexSection, InfiniteScroll, Lazyload, Indicator, Picker, Switch } from 'mint-ui';
import Env from '@/utils/env';
import 'assets/css/base.scss';
import 'assets/css/common.scss';
import 'assets/css/icon.scss';
import 'assets/css/theme.scss';
import router from './router';
import store from './store';
import App from './App.vue';

Es6Promise.polyfill();

// 按需引入部分组件
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Popup.name, Popup);
Vue.component(MessageBox.name, MessageBox);
Vue.component(Switch.name, Switch);
Vue.component(IndexList.name, IndexList);
Vue.component(IndexSection.name, IndexSection);
Vue.component(Cell.name, Cell);
Vue.component(Picker.name, Picker);
Vue.component(Spinner.name, Spinner);
Vue.component(DatetimePicker.name, DatetimePicker);
Vue.use(InfiniteScroll);
Vue.use(Lazyload);

Vue.prototype.$toast = Toast;
Vue.prototype.$loading = Indicator;
Vue.prototype.$message = MessageBox;
Vue.prototype.$isMgtvApp = Env.isMgtvApp;
Vue.config.productionTip = false;
// Vue.directive('title', {
//   inserted(el, binding) {
//     setMetaTitle(binding.value);
//   },
// });

if (Env.isIOS) {
  // 阻止iOS下的橡皮筋效果
  INoBounce.enable();
}

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
