/* eslint-disable no-underscore-dangle,class-methods-use-this */
import qs from 'qs';
import _ from 'lodash';
import 'whatwg-fetch';
import { getMgtvUserTicket, gotoLogin } from 'utils/user';


const METHOD_GET = 'GET';

function objectToFormData(obj, form, namespace) {
  const fd = form || new FormData();
  Object.keys(obj).forEach((key) => {
    let formKey;
    if (namespace) {
      formKey = `${namespace}[${key}]`;
    } else {
      formKey = key;
    }
    const value = obj[key];
    if (value != null) {
      if (typeof value === 'object' && !(value instanceof File)) {
        objectToFormData(value, fd, formKey);
      } else {
        // if it's a string or a File object
        fd.append(formKey, value);
      }
    } else {
      fd.append(formKey, '');
    }
  });
  return fd;
}

function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response;
  }

  const error = new Error(response.statusText);
  error.response = response;
  error.code = 100;
  throw error;
}


class ApiInvoker {
  constructor() {
    this.handledInvalidToken = false;
    this.invalidTokenHandler = function () {};
  }


  request = async (url, data = {}, options = {}) => {
    if (!navigator.onLine) {
      const error = new Error();
      error.code = 1;
      error.message = '无法连接到网络';
      throw error;
    }
    const { method = METHOD_GET } = options;
    const userInfo = await getMgtvUserTicket();
    data.t = new Date().getTime();
    if (!_.isEmpty(userInfo) && userInfo.ticket) {
      data.token = userInfo.ticket;
    }
    let response;
    if (method.toUpperCase() === METHOD_GET) {
      const params = qs.stringify(data);
      const fetchURL = params ? `${url}?${params}` : url;
      response = await fetch(fetchURL, { credentials: 'include' });
      // response = await fetch(fetchURL)
    } else {
      response = await fetch(url, {
        method,
        credentials: 'include',
        // headers: new Headers({ 'Content-Type': 'application/json' }),
        body: objectToFormData(data),
      });
    }

    checkStatus(response);
    const text = await response.text();
    if (url.indexOf('http://d.da.mgtv.com') !== -1) {
      return {
        code: 0,
        data: null,
      };
    }
    /* console.log(url + text); */
    if (text) {
      let json = text;
      if (typeof text === 'string') {
        json = JSON.parse(text);
      }
      if (json.code === 0) {
        return json.data;
      }
      const { msg } = json;
      if (msg && typeof msg === 'string' && msg.indexOf('用户异常') !== -1) {
        gotoLogin();
        return;
      }
      const error = new Error(json.msg);
      error.code = json.code;
      error.message = json.msg;
      throw error;
    }
  }

  loadByImg = (url, conditions) => {
    const imgELE = document.getElementById('uploadImg');
    const params = qs.stringify(conditions);
    const fetchURL = params ? `${url}?${params}` : url;
    imgELE.src = fetchURL;
  }
}

export default new ApiInvoker();
