const { userAgent } = navigator;
const isIOS = /iphone|ipad|itouch|ipod/i.test(userAgent);
const isAndroid = /android/i.test(userAgent);

export default {
  isWeixin: /MicroMessenger/i.test(userAgent) || /weixin/.test(userAgent),
  isMgtvApp: /ImgoTV/i.test(userAgent),
  isIOS,
  isAndroid,
  isWeiBo: /WeiBo/i.test(userAgent),
  isQQ: /QQ/i.test(userAgent),
  isLocal: process.env.VUE_APP_ENV === 'local',
  isPc: !isIOS && !isAndroid,
};

