import ApiInvoker from './apiInvoker';

export const SERVER_URL = `${process.env.VUE_APP_API}/api`;
const requestObjCache = {};
const generateRequest = (module) => {
  let url = `${SERVER_URL}/${module}`;
  if (module === 'UPLOADDATA') { /* 是否数据上报 */
    url = 'http://d.da.mgtv.com';
  }
  let reqObj = requestObjCache[module];
  if (!reqObj) {
    reqObj = (suburl, data, options) => ApiInvoker.request(`${url}${suburl ? `/${suburl}` : ''}`, data, options);
    requestObjCache[module] = reqObj;
  }
  return reqObj;
};
export default generateRequest;
