import generateRequest from './services/request';

const request = generateRequest('Cart');

export function addCart(conditions) {
  return request('add', conditions, { method: 'post' });
}

export function getCartData() {
  return request('index');
}

export function getTotalPrice(conditions) {
  return request('totalCart', conditions, { method: 'post' });
}

export function updateCartNum(conditions) {
  return request('updateNum', conditions, { method: 'post' });
}

export function deleteCart(conditions) {
  return request('delete', conditions, { method: 'post' });
}

export function createSettlement(conditions) { // 生成结算单
  return request('cart2', conditions, { method: 'post' });
}

export function getSettlementInfo(conditions, act = '') { // 获取结算单信息
  return request(`cart3?act=${act}`, conditions, { method: 'post' });
}

export function getGoodsYouLike() { // 热卖
  return request('likeGoods');
}

