import MgtvApi from 'MgtvApi';
/* import Vue from 'vue'; */
import Env from './env';

/* eslint-disable */
/* 微信里设置标题 */
export function setMetaTitle(title) {
  document.title = title;
  const mobile = navigator.userAgent.toLowerCase();
  if (/iphone|ipad|ipod/.test(mobile)) {
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    // 替换成站标favicon路径或者任意存在的较小的图片即可
    iframe.setAttribute("src", "public/favicon.ico");
    const iframeCallback = () => {
      setTimeout(() => {
        iframe.removeEventListener("load", iframeCallback);
        document.body.removeChild(iframe);
      }, 0);
    };
    iframe.addEventListener("load", iframeCallback);
    document.body.appendChild(iframe);
  }
}

/* 是否合法手机号 */
export function isValidMobile(val) {
  return /^(?=\d{11}$)^1(?:3\d|4[57]|5[^4\D]|66|7[^249\D]|8\d|9[89])\d{8}$/.test(
    val
  );
}
/* 设备内生成唯一id */
export function guid() {
  return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, c => {
    const r = (Math.random() * 16) | 0;
    const v = c === "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

export function getCookie(name) {
  const reg = new RegExp(`(^| )${name}=([^;]*)(;|$)`);
  const arr = document.cookie.match(reg);
  if (arr) {
    return unescape(arr[2]);
  }
  return null;
}


/* 路由变化 自动隐藏提示框 */
export function hideMessageBox() {
  const messageELe = document.getElementsByClassName("v-modal");
  if (messageELe.length) {
    messageELe[0].click();
  }
}

/* ios半屏 */
export function isIosHalfWebview() {
  if (Env.isIOS) {
    const {
      body: { clientWidth, clientHeight }
    } = document;
    const {
      screen: { height }
    } = window;
    return (
      Number(clientWidth) === 300 || Number(clientHeight) / Number(height) < 0.7
    );
  }
  return false;
}


/* 字符串转16进制 */
export function strToHexCharCode(str) {
  if (str === "") {
    return "";
  }
  const hexCharCode = ["0x"];
  for (let i = 0; i < str.length; i += 1) {
    hexCharCode.push(str.charCodeAt(i).toString(16));
  }
  return hexCharCode.join("");
}

/* 16进制转字符串 */
export function hexCharCodeToStr(hexCharCodeStr) {
  const trimedStr = hexCharCodeStr.trim();
  const rawStr =
    trimedStr.substr(0, 2).toLowerCase() === "0x"
      ? trimedStr.substr(2)
      : trimedStr;
  const len = rawStr.length;
  if (len % 2 !== 0) {
    console.log("Illegal Format ASCII Code!");
    return "";
  }
  let curCharCode;
  const resultStr = [];
  for (let i = 0; i < len; i += 2) {
    curCharCode = parseInt(rawStr.substr(i, 2), 16); // ASCII Code Value
    resultStr.push(String.fromCharCode(curCharCode));
  }
  return resultStr.join("");
}

